
a {
  text-decoration: none;
}
*,
*:before,
*:after {
  -webkit-box-sizing: border-box;
          box-sizing: border-box;
}
body {
  background: #fafafa;
  font-family: 'Avenir', Helvetica, Arial, sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  color: #555;
  line-height: 1.5;
  font-size: 16px;
  margin: 0;
}
.container {
  width: 900px;
  margin: 0 auto;
  padding: 0 16px;
}

.row-filmCard {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  margin: 0 -16px;
}
.col-filmCard {
  width: 33.33333%;
  padding: 16px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
}
.filmCard {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  text-decoration: none;
  color: inherit;
  overflow: hidden;
  -webkit-box-shadow: 0 1px 2px rgba(0, 0, 0, 0.25);
          box-shadow: 0 1px 2px rgba(0, 0, 0, 0.25);
  border-radius: 8px;
  background: #fff;
}
.filmCard-meta {
  padding: 16px;
}
.filmCard-thumbnail {
  background: #f0f0f0;
  height: 200px;
}
.filmCard-title {
  font-weight: normal;
  font-size: 18px;
  line-height: 1.25;
  margin: 0;
}
.filmCard-producer {
  font-size: 14px;
  color: #aaa;
}

.page {
  background: #fff;
  -webkit-box-shadow: 0 1px 2px rgba(0, 0, 0, 0.25);
          box-shadow: 0 1px 2px rgba(0, 0, 0, 0.25);
  min-height: 100vh;
  border-radius: 8px 8px 0 0;
  overflow: hidden;
}
.page-thumbnail {
  height: 300px;
  background: #333;
}
.page-title {
  margin: 0 0 16px;
  font-size: 32px;
}
.page-paragraph {
  margin: 0 0 32px;
}
.page-content {
  position: relative;
  padding: 0 32px 32px;
}
.page-rating {
  position: absolute;
  top: -50px;
  right: 50px;
  height: 100px;
  width: 100px;
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -webkit-box-pack: center;
      -ms-flex-pack: center;
          justify-content: center;
  -webkit-box-align: center;
      -ms-flex-align: center;
          align-items: center;
  -webkit-box-orient: vertical;
  -webkit-box-direction: normal;
      -ms-flex-direction: column;
          flex-direction: column;
  background: #fff;
  border-radius: 50%;
  -webkit-box-shadow: 0 1px 2px rgba(0, 0, 0, 0.25);
          box-shadow: 0 1px 2px rgba(0, 0, 0, 0.25);
  line-height: 1;
  color: #fff;
  background: #ffc200;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.25);
  overflow: hidden;
}
.page-rating:before {
    content: "";
    position: absolute;
    left: 0;
    right: 0;
    bottom: 0;
    height: 50%;
    background: rgba(255, 158, 0, 0.3);
    -webkit-transform: rotate(45deg);
            transform: rotate(45deg);
    -webkit-transform-origin: top center;
            transform-origin: top center;
}
.page-rating span {
    position: relative;
}
.page-rating-label {
    margin: 8px 0;
}
.page-rating-value {
    font-size: 32px;
    font-weight: bold;
}
.page-info {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  list-style: none;
  padding-left: 0;
  padding-right: 80px;
  margin: 0 -32px 32px;
  background: #a924f5;
  text-shadow: 0 1px 2px rgba(0, 0, 0, 0.3);
  color: #fff;
}
.page-info > li {
    -webkit-box-flex: 1;
        -ms-flex: 1;
            flex: 1;
    padding: 16px 32px;
}
.page-info > li + li {
      border-left: 1px solid rgba(0, 0, 0, 0.075);
}
.page-info > li > span {
      display: block;
}
.page-info > li > span + span {
        margin-top: 8px;
}
.page-info-label {
  font-size: 14px;
  color: rgba(255, 255, 255, 0.75);
  line-height: 1;
}
.page-info-value {
  line-height: 1;
}
.page-item-columns {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  list-style: none;
  padding: 0;
  margin: 0 -33px 0 -32px;
  border-top: 1px solid #eee;
  -webkit-box-shadow: inset 0 -1px 0 0 #eee;
          box-shadow: inset 0 -1px 0 0 #eee;
}
.page-item-columns > li {
    display: -webkit-box;
    display: -ms-flexbox;
    display: flex;
    width: 25%;
    -webkit-box-shadow: -1px -1px 0 0 #eee;
            box-shadow: -1px -1px 0 0 #eee;
}
.page-item {
  -webkit-box-flex: 1;
      -ms-flex: 1;
          flex: 1;
  background: #fff;
  padding: 32px 32px 16px;
  -webkit-box-shadow: inset -1px -1px 0 0 #eee;
          box-shadow: inset -1px -1px 0 0 #eee;
}
.page-item-thumbnail {
  height: 154px;
  margin: 0 0 16px;
  border-radius: 8px;
  background: #f5f5f5;
}
.page-item-name {
  margin: 0 0 16px;
  font-size: 14px;
  text-align: center;
}
.page-item-info {
  display: -webkit-box;
  display: -ms-flexbox;
  display: flex;
  -ms-flex-wrap: wrap;
      flex-wrap: wrap;
  font-size: 13px;
  -webkit-box-pack: justify;
      -ms-flex-pack: justify;
          justify-content: space-between;
  border-top: 1px solid #f5f5f5;
  line-height: 1.25;
  padding: 4px 0;
}
.page-item-info div,
  .page-item-info span {
    padding: 4px 0;
}
.page-item-info span {
    color: #999;
}
